          // console.log(res);
          // alert(res.data.message);
          // 
          if (res.data.success) {
            this.alertMessage = res.data.message;
            this.alertStatus = true;
            setTimeout(
              () => {
                this.alertMessage = '';
                this.alertStatus = false;
              }, 2000,
            );
            // 
            this.getProduct();
            // 
            this.$refs.ReditProductModal.closeModal();
          }
        })
        .catch((err) => {
          // console.log(err);
          this.alertMessage = err.data.message;
          this.alertStatus = false;
          setTimeout(
            () => {
              this.alertMessage = '';
              this.alertStatus = false;
            }, 2000,
          );
        });
    },
  },
  watch: {
    productDataAll() {
      // productDataAllID
      if (this.productDataAll !== undefined && this.productDataAll !== null) {
        // ID
        this.allProductAry = Object.keys(this.productDataAll);
      }
    },
  },
  created() {
    // token
    this.$http.defaults.headers.common.Authorization = this.token;
  },
  mounted() {
    this.isLoading = true;
    // 
    this.getProduct();
  },
};
</script>
